<template>
  <div id="confirm-order">
  	<common-header :title="'确认订单'"></common-header>
    <div class="cell bg-fff" @click="showOT">
      送礼类型
      <span class="fl-right" v-show="orderType===1">存入礼物包</span>
      <span class="fl-right" v-show="orderType===0">实物发货</span>
      <img src="../../assets/arrow_gray.png" class="arrow_gray">
    </div>
    <div class="order-address bg-fff" v-show="orderType===0" @click="linkTo('ConfirmAddress')">
      <div class="address-info">
      	<template v-if="confirmAddress">
	        <div class="name-phone clearfix">
	          <span class="fl-left">收货人：{{confirmAddress.name}}</span>
	          <span class="fl-right">{{getMobile}}</span>
	        </div>
	        <div class="address-detail">
	          {{confirmAddress.street}}
	        </div>
	        <img src="../../assets/arrow_gray.png" class="arrow_gray">
      	</template>
      	<template v-else>未选择</template>
      </div>
      <img src="../../assets/cart/address_bor.png" class="address-bor">
    </div>
    <!--订单详情-->
    <div class="order-detail">
      <!--列表项-->
      <div class="detail-shop bg-fff" v-for="(item,index) in orderList">
        <div class="cell text-over">{{item.orgName}}</div>
        <div class="good-list">
          <div class="list-item" v-for="(goods,index) in item.cartInfos">
            <div class="item-img fl-left">
              <img :src="goods.imagePath" alt="">
            </div>
            <div class="item-info fl-left">
              <div class="title">{{goods.name}}</div>
              <div>{{goods.productSize}}</div>
              <div class="goods-prices">
              	<span class="price"><em style="font-size: 0.32rem">￥</em>{{goods.discountPrice}}</span>
              	<span class="item-count fl-right">x{{goods.buyNum}}</span>
              </div>
            </div>
          </div>
          <div class="cell bg-fff" @click="linkTo('ConfirmCoupon')">
            <span>优惠券</span>
            <span class="span-tag">0张可用</span>
            <span class="fl-right" v-if="false"><em style="color: #999">-</em>￥100.0</span>
            <span class="fl-right" v-else>未选择</span>
            <img src="../../assets/arrow_gray.png" class="arrow_gray">
          </div>
          <div class="cell bg-fff">
            买家留言
            <input type="text" placeholder="选填" class="order-msg fl-right" ref='input' @input="setInputValue" :value="inputs[index]">
          </div>
          <div class="cell bg-fff" style="border: none">
            <span class="fl-right" style="margin-right:0;">合计： <em style="color: #fe4436;">￥{{shopPrices[index]}}</em></span>
          </div>
        </div>
      </div>
    </div>
    <div class="detail-shop order-price bg-fff">
      <div style="margin-bottom: 0.32rem">总金额<span class="fl-right" style="color: #fe4436;"> ￥{{totalPrices}}</span></div>
      <div>优惠券<span class="fl-right" style="color: #fe4436;"> -￥{{totalCoupon}}</span></div>
    </div>
    <div class="footer">
      <div class="fl-right">
        <span class="total-price">实付款：</span>
        <span class="price">￥{{totalPrices-totalCoupon}}</span>
        <span class="goods-account" @click="linkToCashier">提交订单</span>
      </div>
    </div>
    <!-- 底部弹窗 -->
    <div class="shadow" v-if="showOrderType">
      <BottomPopup :itemList="itemList" @selItem="getBottomPopup"></BottomPopup>
    </div>
  </div>
</template>

<script>
  import BottomPopup from '../../components/Popup/BottomPopup'
	
  export default {
    data () {
      return {
        itemList: [
          {
            text: '存入礼物包',
            id: 1
          },
          {
            text: '实物发货',
            id: 0
          }
        ],
        showOrderType: false,
        orderType: 1,
        orderList:[],
        shopPrices:[],
        couponPrices:[],
        inputs:[],
        payment:0,
        confirmAddress:'',
      }
    },
    watch: {},
    components: {
      BottomPopup
    },
    computed:{
    	getMobile(){
    		if(this.confirmAddress){
    			let phone = this.confirmAddress.mobile.toString()
    			let mobile = phone.substring(0,3) + '****' + phone.substring(7,11)
    			return mobile
    		}
    	},
    	//总金额
    	totalPrices(){
    		let totalPrice = 0
    		for(let item of this.shopPrices){
    			totalPrice += item
    		}
    		return totalPrice
    	},
    	//优惠券
    	totalCoupon(){
    		let couponPrice = 0
    		for(let item of this.couponPrices){
    			couponPrice += item
    		}
    		return couponPrice
    	},
    },
    created(){
    	this.initOrderType()
    	this.initOrderList()
    	this.initAddress()
    	this.initInput()
    },
    methods: {
      showOT(){
        this.showOrderType = true
      },
      initOrderList(){
      	let str = localStorage.getItem('orderList')
	    	let orderList = JSON.parse(str)
	    	this.orderList = orderList
	    	
	    	//计算总价
	    	orderList.forEach((res)=>{
	    		let shopPrices = 0
	    		res.cartInfos.forEach((val)=>{
	    			shopPrices += val.buyNum * val.discountPrice
	    		})
	    		this.shopPrices.push(shopPrices)
	    	})
      },
      
      initOrderType(){
      	if(localStorage.getItem('orderType')){
      		let orderType = localStorage.getItem('orderType')
      		this.orderType = parseInt(orderType) 
      	}
      },
      
      initAddress(){
      	//判断是否已经选择
      	if(localStorage.getItem('confirmAddress')){
      		let confirmAddress = JSON.parse(localStorage.getItem('confirmAddress'))
      		this.confirmAddress = confirmAddress
      	}else{
					//获取默认地址
					this.$Api.getShippingAddressList().then((res)=>{
						console.log(res)
						if(res.q.s == 0){
							if(res.q.total > 0){
								let addresses = res.q.addresses
								addresses.forEach((item)=>{
									if(item.selected == 1){
										console.log(item)
										this.confirmAddress = item
									}
								})
							}
						}
					})
      	}
      },
      initInput(){
      	if(localStorage.getItem('inputs')){
      		this.inputs = JSON.parse( localStorage.getItem('inputs') )
      	}
      },
      
      
      setInputValue(){
      	let inputs = this.$refs.input
      	this.inputs = []
      	inputs.forEach((item)=>{
      		this.inputs.push(item.value)
      	})
      	let arr = JSON.stringify(this.inputs)
      	localStorage.setItem('inputs',arr)
      },
      
      //清除缓存
      clearStorage(){
      	localStorage.removeItem('orderType')
      	localStorage.removeItem('orderList')
      	localStorage.removeItem('confirmAddress')
      	localStorage.removeItem('inputs')
      },
      
      getBottomPopup(item){
        localStorage.removeItem('orderType')
        if(item != 'cancel'){
          this.orderTypeText = item.text;
          this.orderType = item.id;
          localStorage.setItem('orderType',item.id)
        }
        this.showOrderType = false
      },
      
      getOrderSubmit(){
      	let orderList = this.orderList
      	let shops = []
				orderList.forEach((item,index)=>{
					let goods = []
      		let shopObj = {}
					shopObj.orgId = item.orgId
      		shopObj.note  = this.inputs[index]
      		shopObj.coupons = ''
					item.cartInfos.forEach((item,index)=>{
      			let goodObj = {}
						goodObj.skuId = item.skuId
	      		goodObj.spuId = item.spuId
	      		goodObj.num = item.buyNum
	      		goodObj.promId  = item.promId
	      		goodObj.customId = item.customId
	      		goods.push(goodObj)
					})
					shopObj.goodsList = goods
      		shops.push(shopObj)
				})
      	console.log(shops)
      	
      	let params = {
      		a:1,
      		t:1,
      		sendType:this.orderType,
      		addresId:this.confirmAddress.id,
      		shops:shops,
      		
      	}
      	console.log(params)
      	this.$Api.getOrderSubmit(params).then((res)=>{
//    		console.log(res)
      	})
      },
      linkToCashier(){
      	this.getOrderSubmit()
      	this.setInputValue()
//    	this.clearStorage()
//      this.$router.push({name: 'Cashier'})
      }
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #confirm-order{
    padding-bottom: 1.6rem;
    min-height: 100%;
    background: #f0f0f0;
    .arrow_gray {
      @include rt-arrow;
    }

    .order-address{
      padding:  0.44rem 0.41rem 0.49rem;
      position: relative;
      font-size: 0.37rem;
      color: #333;

      .address-info{
        width: 100%;
        padding: 0 0.45rem 0 0.7rem;
        background: url("../../assets/cart/ico_location.png") no-repeat center left;
        background-size: 0.43rem auto;
      }
      .address-detail{
        margin-top: 0.35rem;
      }
    }
    .order-address img.address-bor{
      width: 100%;
      height: 0.05rem;
      position: absolute;
      bottom: 0;
      left: 0;
    }
    .detail-shop{
      margin-top: 0.27rem;
    }
    .list-item{
      width: box(750);
      height: box(220);
      border-bottom: solid #ccc 1px;
      padding: box(30);

      .item-img{
        width: 2.13rem;
        height: 2.13rem;
        position: relative;

        img{
          width: 2.13rem;
          height: 2.13rem;
          border-radius: 0.11rem;
        }

      }
      .item-info{
        width: 6.55rem;
        height: 2.13rem;
        position: 0.33rem;
        margin-left: box(30);
        color: #1a1a1a;

        .title{
          font-size: 0.34rem;
          height: 0.9rem;
          @include textflow-two;
          margin-bottom: 0.15rem;
        }
        span{
          font-size: 0.30rem;
          color: #666666;
        }
        .goods-prices{
          margin-top: 0.15rem;

        }
        .price {
          color: $price-color;
          font-size: 0.43rem;
        }
        .item-count{
          font-size: 0.32rem;

        }
      }
    }
    .span-tag{
      border-radius: 5px;
      border: solid 1px #fe4436;
      padding: 0.1rem 0.15rem;
      color: $price-color;
      font-size: 0.27rem;
    }
    .order-msg{
      height: 100%;
      width: 7.5rem;
      text-align: right;
      font-size: 0.4rem;
    }
    .order-price{
      font-size: 0.4rem;
      width: 100%;
      padding: 0.41rem;
    }
    .footer {
      position: fixed;
      right: 0;
      bottom: 0;
      left: 0;
      background: #fff;
      height: 1.31rem;
      line-height: 1.31rem;
      .total-price {
        font-size: 0.4rem;
        color: $text-color;
      }
      .price {
        color: $price-color;
        font-size: 0.4rem;
        margin-right: 0.4rem;
      }
      .goods-account {
        display: inline-block;
        font-size: 0.43rem;
        color: #fff;
      @include bg-color;
        width: 2.91rem;
        height: 1.31rem;
        line-height: 1.31rem;
        text-align: center;
      }
    }
  }

</style>



